.button:hover {
background-color: #3498db;
}

.components {
    /* position: fixed; */
    top: 0;
    width: 250px;
    height: 100vh;
    padding: 20px;
    box-sizing: border-box;
    color: #fff;
    font-family: Arial, sans-serif;
    font-size: 16px;
}

table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    background-color: #2c3e50;
    color: #fff;
  }
  
  th,
  td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  th {
    background-color: #34495e;
  }
  
  tr:nth-child(even) {
    background-color: #34495e;
  }
  